import React from "react"
import { Link } from "gatsby"
import moment from "moment"
import PropTypes from "prop-types"
import styled from "styled-components"

const SectionWrapper = styled.section`
  margin: 0 0 1.5rem 0;
  padding: 0;

  &:last-child {
    margin-bottom: 1rem;
  }
`

const SectionTitle = styled.h2`
  margin: 0;
  padding: 0 0 1rem 0;

  & span {
    font-weight: lighter;
    color: red;
    font-size: 0.8rem;
  }
`

const PostListItem = styled.div`
  margin: 0;
  padding: 0.1rem 0;
`

const PostDate = styled.div`
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0.1rem 1.5rem;
  background-color: #ffcc06;
  border-radius: 0.2rem;
  color: white;
  font-family: monospace;
  font-weight: bold;
`

const PostTitle = styled.div`
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: 0 0 0 0.5rem;

  & a {
    color: black;
  }
`

const PostListSection = ({ title, posts }) => {
  const count = posts.length
  const titleWithCount = `${title} / ${count} post${count === 1 ? "" : "s"}`
  return (
    <SectionWrapper>
      <SectionTitle>{titleWithCount}</SectionTitle>
      {posts.map(({ node }) => {
        const formattedDate = moment(node.frontmatter.date)
          .local()
          .format(`MM/DD`)
        return (
          <PostListItem key={node.id}>
            <PostDate>{formattedDate}</PostDate>
            <PostTitle>
              <Link to={node.fields.slug}>{node.frontmatter.title}</Link>
            </PostTitle>
          </PostListItem>
        )
      })}
    </SectionWrapper>
  )
}

PostListSection.propTypes = {
  title: PropTypes.string,
  posts: PropTypes.arrayOf(
    PropTypes.shape({
      node: PropTypes.shape({
        id: PropTypes.string.isRequired,
        frontmatter: PropTypes.shape({
          title: PropTypes.string.isRequired,
          date: PropTypes.string.isRequired,
        }),
        fields: PropTypes.shape({
          slug: PropTypes.string.isRequired,
        }),
      }),
    }).isRequired
  ),
}

PostListSection.defaultProps = {
  title: ``,
  posts: [],
}

export default PostListSection
