import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Layout from "../components/layout"
import PageHelmet from "../components/pageHelmet"
import PostListSection from "../components/post-list-section"

const PostsPage = () => {
  const data = useStaticQuery(graphql`
    query {
      posts: allMarkdownRemark(
        filter: {frontmatter: {layout: {ne: "draft"}}}
        sort: { order: DESC, fields: frontmatter___date }
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              date
              year: date(formatString: "YYYY")
            }
            fields {
              slug
            }
          }
        }
      }
    }
  `)
  const years = data.posts.edges.map(({ node }) => node.frontmatter.year)
  const uniqueYears = [...new Set(years)]
  const postsInYear = {}
  uniqueYears.forEach(year => {
    postsInYear[year] = data.posts.edges.filter(
      ({ node }) => node.frontmatter.year === year
    )
  })

  return (
    <Layout>
      <PageHelmet title="Posts" />
      {uniqueYears.map(year => (
        <PostListSection key={year} title={year} posts={postsInYear[year]} />
      ))}
    </Layout>
  )
}

export default PostsPage
